* {
  font-family: 'Museo Sans Cyrl', sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.container {
  position: relative;
  width: 1140px;
  margin: 0 auto;
}
.promo {
  background: url("../img/header_bg.jpg") center center / cover no-repeat;
  padding: 21px 0 93px;
}
.promo .container {
  padding: 0 100px;
}
.promo__cap {
  display: flex;
  justify-content: space-between;
}
.promo__item {
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  color: #ffffff;
}
.promo__item span {
  font-size: 12px;
  font-weight: 500;
}
.promo__item:nth-child(3) {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.promo__header {
  margin-top: 91px;
  font-size: 42px;
  font-weight: 700;
  text-align: right;
  line-height: 0.9;
  color: #ffffff;
  text-transform: uppercase;
}
.promo__subheader {
  margin-top: 17px;
  font-size: 24px;
  font-weight: 500;
  text-align: right;
  line-height: 0.9;
  color: #ffffff;
  text-transform: uppercase;
}
.promo__flex {
  margin: 66px 19px 0 0;
  display: flex;
  justify-content: flex-end;
}
.promo__img {
  display: flex;
}
.promo__icons {
  width: 108px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  text-align: center;
  color: #ffffff;
}
.promo__icons img {
  width: 45px;
  height: 45px;
}
.promo__link {
  width: 200px;
  margin-top: 18px;
  margin-left: auto;
  font-size: 16px;
  font-weight: 500;
  color: #ffffff;
}
.promo__link a {
  font-size: 16px;
  font-weight: 500;
  color: #ffffff;
  text-transform: uppercase;
}
.btn {
  display: block;
  width: 150px;
  height: 30px;
  border-radius: 5px;
  background-color: #c70101;
  font-size: 12px;
  font-weight: 500;
  text-align: center;
  color: #ffffff;
  border: none;
  text-transform: uppercase;
}
.btn_promo {
  width: 300px;
  height: 50px;
  font-size: 16px;
  margin-top: 67px;
  margin-left: auto;
}
.btn_consultation {
  width: 300px;
  height: 55px;
  font-size: 16px;
  margin-top: 5px;
}
.btn_catalog {
  width: 131px;
  height: 40px;
  font-size: 14px;
}
.preference {
  padding: 70px 0 95px;
}
.preference__title {
  font-size: 30px;
  font-weight: 700;
  text-align: center;
  color: #0d0d0d;
}
.preference__wrapper {
  margin: 65px auto 0;
  display: flex;
  justify-content: space-between;
}
.preference__item {
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  font-weight: 300;
  text-align: center;
  color: #0d0d0d;
}
.preference__item img {
  width: 70px;
}
.preference__item h3 {
  font-size: 18px;
  font-weight: 700;
  color: #0d0d0d;
}
.preference__item span {
  color: #ff0000;
  text-transform: uppercase;
}
.consultation {
  padding: 100px 0 92px;
  background: url("../img/consultation_bg.jpg") center center / cover no-repeat;
}
.consultation .container {
  padding-left: 102px;
}
.consultation__title {
  font-size: 36px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
}
.consultation__description {
  margin-top: 80px;
  font-size: 20px;
  font-weight: 500;
  color: #ffffff;
}
.consultation__form {
  margin-top: 36px;
  width: 300px;
}
.consultation__form input {
  width: 100%;
  height: 50px;
  margin-bottom: 15px;
  border-radius: 5px;
  padding-left: 20px;
  background-color: #ffffff;
  font-size: 14px;
  font-weight: 300;
  color: #4b4b4b;
}
.carousel {
  padding: 81px 0;
}
.carousel__inner {
  position: relative;
  width: 750px;
  margin: 0 auto;
}
.carousel__inner img {
  height: 527px;
}
.carousel .tns-item img {
  display: block;
  margin: 0 auto;
}
.carousel .prev,
.carousel .next {
  background-color: rgba(0, 0, 0, 0);
  width: 31px;
  height: 50px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 66px;
  border: none;
}
.carousel .next {
  left: auto;
  right: 66px;
}
.catalog__title {
  font-size: 30px;
  font-weight: 700;
  text-align: center;
  color: #0d0d0d;
  text-transform: uppercase;
}
.catalog__tabs {
  margin: 70px auto 0;
  list-style-type: none;
  display: flex;
  justify-content: center;
}
.catalog__tab {
  width: 200px;
  height: 50px;
  border: solid 2px #d0d0d0;
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #6b6b6b;
  text-transform: uppercase;
  margin: 0 5px;
  transform: skewX(-30deg);
}
.catalog__tab div {
  transform: skewX(30deg);
}
.catalog__tab:hover {
  box-shadow: 3px 3px 20px #c70101;
  border: none;
}
.catalog__tab_active {
  background-color: #c70101;
  color: #ffffff;
  border: solid 2px #c70101;
}
.catalog__content {
  display: none;
  flex-wrap: wrap;
  margin-top: 49px;
}
.catalog__content hr {
  width: 260px;
  border: 1px solid black;
  border-top: none;
  margin: 0 auto;
}
.catalog__content_active {
  display: flex;
  justify-content: center;
}
.catalog__item {
  width: 300px;
  height: 440px;
  border: solid 2px #e8e8e8;
  background-color: #ffffff;
  overflow: hidden;
  margin: 10px;
}
.catalog__top {
  display: flex;
  width: calc(200% + 40px);
}
.catalog__front {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
  padding: 20px 0;
  transform: translateX(-103%);
  margin-right: 0;
}
.catalog__front_active {
  transform: translateX(0%);
  margin-right: 20px;
}
.catalog__description {
  width: 225px;
  font-size: 14px;
  font-weight: 300;
  text-align: center;
  color: #0d0d0d;
  margin-bottom: auto;
}
.catalog__description h3 {
  margin-bottom: 0;
  font-size: 16px;
  font-weight: 700;
  text-align: center;
  color: #0d0d0d;
}
.catalog__link,
.catalog__return {
  font-size: 14px;
  font-weight: 500;
  color: #c70101;
}
.catalog__link:hover,
.catalog__return:hover {
  color: #c70101;
}
.catalog__back {
  width: 50%;
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: translateX(0%);
  margin-left: 20px;
}
.catalog__back_active {
  transform: translateX(-100%);
  margin-left: 0;
}
.catalog__back ul {
  width: 240px;
  list-style-type: none;
  font-size: 14px;
  font-weight: 300;
  color: #555555;
  margin-bottom: auto;
}
.catalog__back ul li {
  display: block;
  margin-bottom: 10px;
  position: relative;
}
.catalog__back ul li:before {
  content: '';
  width: 5px;
  height: 5px;
  background-color: #c70101;
  position: absolute;
  left: -14px;
  top: 7px;
}
.catalog__bottom {
  display: flex;
  padding: 21px 0 27px 20px;
}
.catalog__price {
  font-size: 19px;
  font-weight: 700;
  color: #050505;
  line-height: 0.97;
  margin-right: 32px;
}
.catalog__price span {
  margin-right: 32px;
  font-size: 14px;
  font-weight: 300;
  color: #555555;
  text-decoration: line-through;
}
.reviews {
  padding: 103px 0 61px;
}
.reviews__title {
  font-size: 30px;
  font-weight: 700;
  text-align: center;
  color: #0d0d0d;
  text-transform: uppercase;
}
.reviews__wrapper {
  margin: 70px 0 0 97px;
}
.reviews__item {
  display: flex;
  margin-bottom: 100px;
}
.reviews__item:last-child {
  margin-bottom: 0;
}
.reviews__img {
  width: 169px;
  height: 169px;
  border-radius: 100%;
  margin-right: 62px;
}
.reviews__img_1 {
  background: url("../../icons/ava_1.png") center center / cover no-repeat;
}
.reviews__img_2 {
  background: url("../../icons/ava_2.png") center center / cover no-repeat;
}
.reviews__img_3 {
  background: url("../../icons/ava_3.png") center center / cover no-repeat;
}
.reviews__description {
  width: 650px;
  font-size: 16px;
  font-weight: 300;
  color: #0d0d0d;
}
.reviews__description span {
  display: block;
  font-size: 14px;
  font-weight: 300;
  color: #646464;
  margin-bottom: -10px;
}
.reviews__description h3 {
  font-size: 18px;
  font-weight: 700;
  color: #0d0d0d;
  margin-bottom: 0;
}
.footer {
  position: relative;
}
.footer__wrapper {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 460px;
  height: 270px;
  padding: 50px 0 60px ;
  background-color: #c70101;
}
.footer__adr {
  font-size: 20px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
}
.footer__tel {
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  color: #ffffff;
  margin-top: 32px;
}
.footer__socials {
  margin-top: 38px;
  display: flex;
  justify-content: center;
}
.footer__socials a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border: white 2px solid;
  border-radius: 100%;
  margin-right: 30px;
}
.footer__socials a:last-child {
  margin-right: 0;
}
@media (max-width: 1200px) {
  .container {
    width: 100%;
  }
  .carousel .prev {
    top: auto;
    bottom: -70px;
    left: 40%;
  }
  .carousel .next {
    top: auto;
    bottom: -70px;
    right: 40%;
  }
}
@media (max-width: 991px) {
  .promo .container {
    padding: 0 10px;
  }
  .preference__wrapper {
    padding: 0 10px;
  }
  .consultation .container {
    padding-left: 20px;
  }
  .consultation__title {
    font-size: 30px;
  }
  .carousel .tns-item {
    width: 750px;
  }
  .carousel__inner img {
    height: auto;
  }
  .reviews {
    padding: 103px 0 300px 0;
  }
  .reviews__wrapper {
    margin: 70px 10px 0;
  }
  .reviews__img {
    width: 25%;
    margin-right: 10px;
  }
  .reviews__description {
    width: 70%;
  }
  .footer__wrapper {
    top: -270px;
  }
}
@media (max-width: 767px) {
  .promo__cap {
    align-items: center;
  }
  .promo__item img {
    width: 30vw;
  }
  .preference__item {
    font-size: 10px;
  }
  .preference__item h3 {
    font-size: 15px;
  }
  .consultation__title {
    font-size: 25px;
  }
  .carousel .tns-item {
    width: 550px;
  }
  .carousel .tns-item img {
    width: 100%;
  }
  .carousel .prev {
    left: 30%;
  }
  .carousel .next {
    right: 30%;
  }
  .catalog__tab {
    width: 170px;
  }
}
@media (max-width: 575px) {
  .btn {
    width: 30vw;
    font-size: 10px;
  }
  .btn_promo {
    width: 60vw;
    font-size: 12px;
  }
  .btn_consultation {
    width: 270px;
    font-size: 12px;
  }
  .promo__item {
    font-size: 10px;
  }
  .promo__item img {
    width: 27vw;
  }
  .promo__item span {
    font-size: 9px;
  }
  .promo__header {
    font-size: 25px;
  }
  .promo__subheader {
    font-size: 14px;
  }
  .promo__icons {
    width: 90px;
    font-size: 10px;
  }
  .preference__title {
    font-size: 25px;
  }
  .preference__wrapper {
    flex-direction: column;
    width: 90vw;
  }
  .preference__item {
    width: 100%;
    font-size: 13px;
    align-items: center;
  }
  .consultation__title {
    font-size: 20px;
  }
  .consultation__description {
    font-size: 14px;
    margin-top: 30px;
  }
  .consultation__form {
    width: 270px;
  }
  .catalog__tab {
    height: 40px;
    width: 90px;
    font-size: 10px;
  }
  .reviews__item {
    flex-direction: column;
  }
  .reviews__description {
    width: 100%;
  }
  .reviews__img {
    width: 50%;
    margin-right: 0;
    margin: 0 auto 10px;
  }
  .footer__wrapper {
    width: 310px;
  }
  .footer__adr {
    font-size: 18px;
  }
}
